<template>
  <div style="">
    <van-field
      label="账单类型"
      readonly
      style="margin-top:10px;font-size:0.8rem;"
      :value="$route.query.title"
    />
    <div
      v-if="$route.query.title == '生活账单'"
      class="round"
    >
      <div>缴费周期</div>
      <div style="color:#989898;">{{ dataForm.startTime | dataFilter('YYYY.MM.DD') }} ~ {{ dataForm.endTime | dataFilter('YYYY.MM.DD') }}</div>
    </div>
    <div
      v-if="$route.query.title == '生活账单'"
      class="round_title"
    >
      生活缴费周期和房租账单周期同步，不支持修改。
    </div>
    <div
      v-if="$route.query.title == '房租账单'"
      class="round"
      style="padding: 12px 16px 8px;"
    >
      <div>缴费周期</div>
      <div style="color:#989898;">{{ dataForm.startTime | dataFilter('YYYY.MM.DD') }} ~
        <span
          @click="editRoomEndTime"
          style="color:#ff7e40;"
        > {{ dataForm.endTime | dataFilter('YYYY.MM.DD') }}</span>
        <van-icon
          name="arrow"
          style="vertical-align:middle;"
        />
      </div>
    </div>
    <van-field
      v-if="$route.query.title == '房租账单'"
      v-model="dataForm.roomPrice"
      label="租金"
      type="number"
      style="margin-top:20px;"
      placeholder="请输入租金金额"
      :value="price"
    />
    <van-field
      v-if="$route.query.title == '房租账单' && $route.query.origin == '1'"
      v-model="dataForm.deposit"
      label="押金"
      type="number"
      placeholder="请输入押金金额"
      :value="price"
    />
    <div
      id='bottom'
      @click="commit"
    >确定</div>
    <div
      v-if="$route.query.title == '生活账单'"
      class="editBox"
    >
      <!-- 费用项目 -->
      <div class="local_info">
        <span>费用项目</span>
        <img
          @click="addRight('add')"
          class="img_add_delete"
          src="@/assets/five/add.png"
          alt
        />
      </div>
      <!-- 费用列表 -->
      <div>
        <table
          v-for="item in lifeCostLists"
          :key="item.id"
          class="info_box"
          :style="{'padding':(item.checkType < 4)?'4px 20px':'14px 20px'}"
        >
          <tr>
            <!-- 左边 -->
            <td>
              <img
                @click="remove(item)"
                class="img_add_delete"
                src="@/assets/five/delete.png"
                alt
              />
              <div class="price_unit">
                <div>
                  <span
                    class="three"
                    :class="(item.checkType < 4)?'w000':''"
                    :style="{'padding-bottom':(item.checkType < 4)?'0.2rem':'0'}"
                  >{{ lifeTypes[item.checkType] }}</span>
                  <span v-if="item.checkType == '1' && item.elecMode == '抄表计算'
                    || item.checkType == '2' && item.waterMode == '抄表计算' 
                    || item.checkType == '3' && item.gasMode == '抄表计算'">{{ item.price }}{{ unit[item.checkType] }}</span>
                </div>
                <div v-if="item.checkType=='1' && item.elecMode=='抄表计算'
                      || item.checkType=='2' && item.waterMode=='抄表计算'
                      || item.checkType=='3' && item.gasMode=='抄表计算'">
                  <span v-if="item.preDegree">本期用{{ obj[item.checkType] }}:</span>
                  <span v-if="item.preDegree">
                    0 {{ unit1[item.checkType] }}&emsp;({{ item.preDegree }}-{{ item.preDegree }})</span>
                </div>
                <div v-else>
                </div>
              </div>
            </td>
            <!-- 右边  -->
            <td
              v-if="item.checkType=='1' && item.elecMode=='抄表计算'
            || item.checkType=='2' && item.waterMode=='抄表计算'
            || item.checkType=='3' && item.gasMode=='抄表计算'"
              @click="setItem(item,'edit')"
              class="f14"
            >
              <span
                class="c000"
                v-if="(item.degree-item.preDegree)*item.price < 0"
              >0</span>
              <span
                class="c000"
                v-else
              >{{ ((item.degree-item.preDegree)*item.price).toFixed(2) }}</span>
              <van-icon
                name="arrow"
                class="cb2"
              />
            </td>
            <!-- 右边  -->
            <td
              v-else
              class="f14"
              @click="setItem(item,'edit')"
            >
              <span>
                <span class="c000">{{ item.price }}</span>
                <span v-if="item.checkType == '1'">{{ item.elecMode == '预付费模式'?unit1[item.checkType]:'元' }}</span>
                <span v-else-if="item.checkType == '2'">{{ item.waterMode == '预付费模式'?unit1[item.checkType]:'元' }}</span>
                <span v-else-if="item.checkType == '3'">{{ item.gasMode == '预付费模式'?unit1[item.checkType]:'元' }}</span>
                <span v-else>元</span>
                <van-icon
                  v-if=" item.checkType < '4'"
                  name="arrow"
                  class="cb2"
                />
              </span>
            </td>
          </tr>
        </table>
      </div>
    </div>
    <van-popup
      v-model="addShow"
      class="addBox"
    >
      <div>
        <!-- 费用项目 -->
        <p
          class="add-title"
          v-if="objItem"
        >{{ obj[objItem.checkType] }}费详情</p>
        <p
          class="add-title"
          v-else
        >添加费用</p>
        <div
          class="add-obj"
          v-if="!objItem"
        >
          <div>费用项目</div>
          <div @click="addObjShow = true">
            <span>{{ title }}</span>
            <van-icon
              name="arrow"
              class="cb2"
            />
          </div>
        </div>
        <!-- 收费方式 -->
        <div class="add-obj">
          <div>收费方式</div>
          <div
            v-if="checkType < 4"
            @click="add_or_edeit == 'add' ?show_lifeCostPay = true:''"
          >
            <span>{{ lifeCost_mode }}</span>
            <van-icon
              v-if="add_or_edeit == 'add'"
              name="arrow"
              class="cb2"
            />
          </div>
          <div v-else>固定收取</div>
        </div>
        <!-- 单价 -->
        <div class="add-obj">
          <span class="w30">单价</span>
          <input
            @keyup="clearNoNum(addPrice,name = 'price')"
            type="text"
            v-model="addPrice"
            placeholder="请输入单价"
            class="addInput60"
          >
          <div
            v-if="objItem && lifeCost_mode == '固定收取'"
            class="w10"
          >{{ lifeCost_mode == '抄表计算' ? unit[objItem.checkType]:'元/月' }}</div>
          <div
            v-else-if="objItem && lifeCost_mode != '固定收取'"
            class="w10"
          >{{ lifeCost_mode == '抄表计算' ? unit[checkType]:unit1[checkType] }}</div>
          <div
            v-else-if="!objItem && lifeCost_mode == '固定收取'"
            class="w10"
          >元/月</div>
          <div
            v-else-if="!objItem && lifeCost_mode != '固定收取'"
            class="w10"
          >{{ lifeCost_mode == '抄表计算' ? unit[checkType]:unit1[checkType] }}</div>
        </div>
        <!-- 上次读数 -->
        <div
          class="add-obj"
          v-if="checkType < 4 && lifeCost_mode == '抄表计算'"
        >
          <span class="w30 ">上次读数</span>
          <input
            v-if="objItem"
            readonly
            type="text"
            v-model="preDegree"
            placeholder="请输入上次读数"
            class="addInput60 cb2"
          >
          <input
            v-else
            @keyup="clearNoNum(preDegree,name = 'lastNum')"
            type="text"
            v-model="preDegree"
            placeholder="请输入上次读数"
            class="addInput60"
          >
          <div class="w10"></div>
        </div>
        <!-- 应收费用 -->
        <div
          class="add-obj"
          v-if="objItem && lifeCost_mode == '抄表计算'"
        >
          <span class="w30">应收费用</span>
          <span
            class="addInput60 c60"
            v-if="degree"
          >{{ (degree - preDegree) * addPrice | filterPrice }}&ensp;<span class="w10">元</span></span>
          <span
            class="addInput60 c60"
            v-else
          >0 &ensp;<span class="w10">元</span></span>
          <div class="w10"></div>
        </div>
        <div class="add-button">
          <div
            class="add-cancle"
            @click="addShow = false"
          >取消</div>
          <div
            class="add-sure"
            @click="addSure"
          >确定</div>
        </div>
      </div>
    </van-popup>
    <!-- 选择-费用项目 -->
    <van-popup
      v-model="addObjShow"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="lifeCostAll"
        @cancel="addObjShow = false"
        @confirm="onConfirm"
      />
    </van-popup>
    <!-- 选择-收费方式 -->
    <van-popup
      v-model="show_lifeCostPay"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="payAll"
        @confirm="onConfirmCostPayType"
        @cancel="show_lifeCostPay = false"
      />
    </van-popup>
    <!-- 点击选择时间---结束时间 -->
    <mt-popup
      v-model="show_eTime"
      position="bottom"
      style="width:100%;"
    >
      <van-datetime-picker
        type="date"
        title="选择结束时间"
        v-model="endT"
        :min-date='minDate'
        :max-date='maxDate'
        @cancel='show_eTime = false'
        @confirm="onInputAfterTime"
      />
    </mt-popup>
  </div>
</template>
<script>
import dayjs from 'dayjs';
import { GetSystemDictDataByType } from '@/api/api_query';
import { PostBusinessLifeCostBillAdd, PostBusinessLifeCostBillEdit, GetBusinessLifeCostBillLastByorderroom } from '@/api/api_business_hotel_and_scale';
import { POSTBusinessRentalBillLastEdit } from '@/api/api_bill';
import { Toast } from 'vant';
export default {
    data() {
        return {
            price: null,
            roundTime: null,
            minDate: new Date(),
            maxDate: new Date(),
            startT: null,
            endT: null,
            isShow: false,
            show_eTime: false,
            dataForm: {
                orderId: this.$route.query.orderId,
                orderRoomId: this.$route.query.orderRoomId,
                id: this.$route.query.rentalBillId || null,
                roomPrice: null,
                deposit: null,
                startTime: null,
                endTime: null
            },
            lifeTypes: {},
            lifeCosts: [],
            lifeCostAll: [],
            lifeCostLists: [],
            editPrice: {},
            obj: { 1: '电', 2: '水', 3: '气' },
            unit: { 1: '元/度', 2: '元/吨', 3: '元/m³' },
            unit1: { 1: '度', 2: '吨', 3: 'm³' },
            addShow: false,
            addPrice: '',
            addObjShow: false,
            show_lifeCostPay: false,
            columns: [],
            payAll: [],
            title: '',
            dictSort: '',
            objItem: '',
            detailPrice: '',
            preDegree: null,
            degree: null,
            lifeCost_mode: null,
            add_or_edeit: null,
            checkType: null
        };
    },
    filters: {
        filterPrice(price) {
            return Number(price).toFixed(2);
        }
    },
    created() {
        this.$store.commit('setHeader', this.$route.query.rentalBillId ? '编辑账单' : '添加账单');
        if (this.$route.query.title == '生活账单') {
            this.getMore();
            this.dataForm.startTime = dayjs(this.$route.query.startTime).format('YYYY-MM-DD');
            this.d_endTime = dayjs(this.$route.query.endTime).format('YYYY-MM-DD');
            this.dataForm.endTime = dayjs(this.$route.query.endTime).format('YYYY-MM-DD');
        }
        if (this.$route.query.rentalBillId && this.$route.query.title == '房租账单') {
            this.dataForm.startTime = this.$route.query.startTime;
            this.dataForm.endTime = this.$route.query.endTime;
            this.d_endTime = this.$route.query.endTime;
            this.dataForm.roomPrice = this.$route.query.roomPrice;
            this.dataForm.deposit = this.$route.query.deposit;
            this.dataForm.orderId = this.$route.query.orderId;
            let allDays = dayjs(this.dataForm.endTime).diff(dayjs(this.dataForm.startTime), 'day');
            this.dayPrice = Number(this.dataForm.roomPrice / allDays);
        }
    },
    methods: {
        // 展开-房费-结束日期
        editRoomEndTime() {
            let c_time = dayjs().unix();
            let d_stime = dayjs(this.dataForm.startTime).unix();
            let d_etime = dayjs(this.d_endTime);
            let s_time = c_time > d_stime ? dayjs() : dayjs(this.dataForm.startTime);
            this.minDate = new Date(dayjs(s_time).add(1, 'day').format('YYYY-MM-DD'));
            this.maxDate = new Date(dayjs(d_etime).format('YYYY-MM-DD'));
            this.show_eTime = true;
        },
        // 选择结束时间段-after
        onInputAfterTime(e) {
            this.dataForm.endTime = dayjs(e).format('YYYY-MM-DD');
            if (this.$route.query.title == '房租账单') {
                let d_allDays = dayjs(this.dataForm.endTime).diff(dayjs(this.dataForm.startTime), 'day');
                this.dataForm.roomPrice = Number(this.dayPrice * (d_allDays + 1)).toFixed(2);
            }
            this.show_eTime = false;
        },
        // 数据查询-长租生活费用
        getMore() {
            GetSystemDictDataByType({ dictType: 'tb_business_life_cost_mode' }).done((res) => {
                this.payOldAll = res.data;
                this.payAll = res.data.map((item) => item.dictLabel);
                this.lifeCost_mode = this.payOldAll[0].dictLabel;
            });
            GetSystemDictDataByType({ dictType: 'business_life_cost' }).done((res) => {
                this.oldLifeCosts = res.data;
                this.lifeCostAll = res.data.map((item) => item.dictLabel);
                this.title = this.oldLifeCosts[0].dictLabel;
                this.lifeTypes = {};
                res.data.map((item) => {
                    this.lifeTypes[item.dictSort] = item.dictLabel;
                });
            });
        },
        // 匹配
        clearNoNum(value, name) {
            value = value.toString();
            value = value.replace(/[^\d.]/g, ''); //清除“数字”和“.”以外的字符
            value = value.replace(/\.{2,}/g, '.'); //只保留第一个. 清除多余的
            value = value.replace('.', '$#$').replace(/\./g, '').replace('$#$', '.');
            value = value.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'); //只能输入两个小数
            if (value.indexOf('.') < 0 && value != '') {
                //以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
                value = parseFloat(value);
            }
            if (name == 'price') {
                this.addPrice = value;
            } else if (name == 'lastNum') {
                this.preDegree = value;
            } else if (name == 'nowNum') {
                this.degree = value;
            }
        },
        // 费用项目-添加
        addRight(type) {
            this.add_or_edeit = type;
            if (this.lifeCostLists.length > 0) {
                let _list = [];
                this.oldLifeCosts.forEach((item) => {
                    let lt = this.lifeCostLists.map((itm) => itm.checkType);
                    if (!lt.includes(item.dictSort)) {
                        _list.push(item.dictLabel);
                    }
                });
                this.lifeCostAll = _list;
            }
            if (this.lifeCostAll.length > 0) {
                this.addShow = true;
                this.title = this.lifeCostAll[0];
                this.checkType = this.oldLifeCosts.find((item) => item.dictLabel == this.title).dictSort;
                this.objItem = '';
                this.addPrice = '';
                this.preDegree = '';
                this.degree = '';
            } else {
                Toast({
                    message: '暂无可添加费用项目',
                    duration: 900
                });
                return;
            }
        },
        // 选择-费用项目
        onConfirm(e) {
            this.title = e;
            this.checkType = this.oldLifeCosts.find((item) => item.dictLabel == e).dictSort;
            this.addObjShow = false;
        },
        // 选择-收费方式
        onConfirmCostPayType(e) {
            this.lifeCost_mode = e;
            let mode = this.payOldAll.find((item) => item.dictLabel == e).dictValue;
            if (this.checkType == 1) {
                this.elecMode = mode;
            } else if (this.checkType == 2) {
                this.waterMode = mode;
            } else if (this.checkType == 3) {
                this.gasMode = mode;
            }
            this.show_lifeCostPay = false;
        },
        // 新增或编辑-确定
        addSure() {
            if (!this.addPrice) {
                Toast({
                    message: '请输入单价',
                    duration: 900
                });
                return;
            }
            if (this.checkType < 4) {
                if (!this.preDegree && this.lifeCost_mode == '抄表计算' && this.add_or_edeit == 'add') {
                    Toast({
                        message: '请输入上次读数',
                        duration: 900
                    });
                    return;
                }
            }
            if (!this.objItem) {
                // 新增
                this.addSave();
            } else {
                // 编辑
                this.lifeCostLists.find((item) => item.checkType == this.objItem.checkType).price = Number(this.addPrice).toFixed(2);
                this.lifeCostLists.find((item) => item.checkType == this.objItem.checkType).degree = Number(this.degree).toFixed(2);
                this.lifeCostLists.find((item) => item.checkType == this.objItem.checkType).preDegree = Number(this.preDegree).toFixed(2);
                this.addShow = false;
            }
        },
        // 生活账单-新增、编辑项目
        addSave() {
            let data = {
                checkType: this.checkType,
                price: Number(this.addPrice).toFixed(2)
            };
            if (this.checkType < 4) {
                data.preDegree = Number(this.preDegree).toFixed(2);
                data.degree = Number(this.degree).toFixed(2);
            }
            if (this.checkType == 1) {
                data.elecMode = this.lifeCost_mode;
                if (this.lifeCost_mode == '2') {
                    delete data.degree;
                    delete data.preDegree;
                }
            } else if (this.checkType == 2) {
                data.waterMode = this.lifeCost_mode;
                if (this.lifeCost_mode == '2') {
                    delete data.degree;
                    delete data.preDegree;
                }
            } else if (this.checkType == 3) {
                data.gasMode = this.lifeCost_mode;
                if (this.lifeCost_mode == '2') {
                    delete data.degree;
                    delete data.preDegree;
                }
            }
            this.lifeCostLists.push(data);
            this.addShow = false;
        },
        // 生活账单移除项目
        remove(item) {
            this.lifeCostLists = this.lifeCostLists.filter((itm) => itm.checkType != item.checkType);
        },
        // 编辑
        setItem(item, type) {
            this.add_or_edeit = type;
            if (item.checkType < 4) {
                this.addShow = true;
                this.objItem = item;
                this.preDegree = Number(this.objItem.preDegree).toFixed(2);
                this.addPrice = Number(this.objItem.price).toFixed(2);
                if (this.objItem.degree == '0.00') {
                    this.degree = '';
                } else {
                    this.degree = Number(this.objItem.degree).toFixed(2);
                }
                this.checkType = this.objItem.checkType;
                if (this.objItem.checkType == 1) {
                    this.lifeCost_mode = this.objItem.elecMode;
                } else if (this.objItem.checkType == 2) {
                    this.lifeCost_mode = this.objItem.waterMode;
                } else if (this.objItem.checkType == 3) {
                    this.lifeCost_mode = this.objItem.gasMode;
                }
                this.detailPrice = (this.Degree - this.preDegree) * this.addPrice;
            }
        },
        // 提交账单
        saveBack() {
            let data = {};
            data.id = this.$route.query.lifeCostBillId;
            this.lifeCostLists.map((item) => {
                if (item.dictSort < 4) {
                    if (item.dictSort == 1 && item.elecMode == '1') {
                        data.elecDegree = this.lifeCostLists.find((item) => item.dictSort == 1).Degree;
                        data.elecMode = item.elecMode;
                    } else if (item.dictSort == 2 && item.waterMode == '1') {
                        data.waterDegree = this.lifeCostLists.find((item) => item.dictSort == 2).Degree;
                        data.waterMode = item.waterMode;
                    } else if (item.dictSort == 3 && item.gasMode == '1') {
                        data.gasDegree = this.lifeCostLists.find((item) => item.dictSort == 3).Degree;
                        data.gasMode = item.gasMode;
                    }
                }
                data[item.dictValue] = item.Price;
            });
            MessageBox.confirm('', {
                message: '确认提交账单？',
                title: '提示',
                cancelButtonText: '取消',
                confirmButtonText: '确定'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        PostBusinessLifeCostBillEdit(data).done((res) => {
                            Toast({
                                message: res.msg,
                                duration: 900
                            });
                            setTimeout('window.history.go(-1)', 900);
                            localStorage.removeItem('life_fee');
                        });
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        },
        // 确定
        commit() {
            if (this.$route.query.title == '房租账单') {
                if (!this.dataForm.endTime) {
                    Toast({
                        message: '请选择结束日期',
                        duration: 900
                    });
                    return;
                }
                if (!this.dataForm.roomPrice) {
                    Toast({
                        message: '请输入租金金额',
                        duration: 900
                    });
                    return;
                }
                if (this.$route.query.rentalBillId) {
                    this.dataForm.endTime = dayjs(this.dataForm.endTime).format('YYYY-MM-DD');
                    delete this.dataForm.orderRoomId;
                    delete this.dataForm.startTime;
                    POSTBusinessRentalBillLastEdit(this.dataForm).done((res) => {
                        Toast({
                            message: res.msg,
                            duration: 900
                        });
                        setTimeout('window.history.go(-1)', 900);
                    });
                }
            } else {
                let aaa = [];
                this.lifeCostLists.map((item) => {
                    let obj = {};
                    item.dictValue = this.oldLifeCosts.find((itm) => itm.dictSort == item.checkType).dictValue;
                    if (item.checkType < 4) {
                        if (item.elecMode == '固定收取' || item.waterMode == '固定收取' || item.gasMode == '固定收取') {
                            delete item.degree;
                            delete item.preDegree;
                        }
                        if (item.elecMode) {
                            item.mode = this.payOldAll.find((itm) => itm.dictLabel == item.elecMode).dictValue;
                            delete item.elecMode;
                        } else if (item.waterMode) {
                            item.mode = this.payOldAll.find((itm) => itm.dictLabel == item.waterMode).dictValue;
                            delete item.waterMode;
                        } else if (item.gasMode) {
                            item.mode = this.payOldAll.find((itm) => itm.dictLabel == item.gasMode).dictValue;
                            delete item.gasMode;
                        }
                        delete item.checkType;
                        obj[item.dictValue] = item;
                    } else {
                        obj[item.dictValue] = item;
                    }
                    aaa.push(obj);
                });
                this.aaaaobj = {};
                aaa = aaa.map((item) => {
                    for (const key in item) {
                        if (item[key].dictValue == 'waterPrice' || item[key].dictValue == 'elecPrice' || item[key].dictValue == 'gasPrice') {
                            delete item[key].dictValue;
                            this.aaaaobj[key] = item[key];
                        } else {
                            delete item[key].dictValue;
                            this.aaaaobj[key] = item[key].price;
                        }
                    }
                });
                let life_data = {
                    orderId: this.dataForm.orderId,
                    orderRoomId: this.dataForm.orderRoomId,
                    lifeCost: JSON.stringify(this.aaaaobj)
                };
                PostBusinessLifeCostBillAdd(life_data).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 1000
                    });
                    setTimeout('window.history.go(-1)', 900);
                });
            }
        }
    }
};
</script>
